import React, { useEffect, useMemo, useRef, useState } from 'react';
import { BookOpen, FileText, Eye, Maximize2, Minimize2, ZoomIn, ZoomOut, Maximize, Columns } from 'lucide-react';
import Card from '../components/Card';
import SectionHeader from '../components/SectionHeader';
import Button from '../components/Button';

// react-pdf (pdf.js)
import { Document, Page, pdfjs } from 'react-pdf';

// CRA: use classic JS worker served from public/ (ensure it exists)
pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.min.js';

// Keep this constant at module scope and avoid spaces in filenames if possible
const BANKS = [
  {
    slug: 'environment',
    title: 'Environment Awareness  Question Bank',
    description: 'Topics: climate change, biodiversity, conservation, sustainability.',
    // Prefer filenames without spaces; if you must keep spaces, ensure path is exact.
    file: '/question-banks/INDEX.pdf',
  },
  {
    slug: 'health',
    title: 'Natural Health & Wellness  Question Bank',
    description: 'Topics: yoga, naturopathy, lifestyle, preventive well-being.',
    file: '/question-banks/INDEX.pdf',
  },
  {
    slug: 'farming',
    title: 'Sustainable Farming  Question Bank',
    description: 'Topics: organic inputs, soil health, natural pest control.',
    file: '/question-banks/INDEX.pdf',
  },
];

/* Responsive + Fullscreen PDF viewer */
const PdfJsViewer = ({ src }) => {
  const [numPages, setNumPages] = useState(null);
  const [error, setError] = useState('');
  const [scale, setScale] = useState(1);             // zoom factor (0.53)
  const [fitMode, setFitMode] = useState('width');   // 'width' | 'page'
  const shellRef = useRef(null);
  const scrollRef = useRef(null);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [containerWidth, setContainerWidth] = useState(0);

  // Disable right-click and save/print/open shortcuts
  useEffect(() => {
    const preventKeys = (e) => {
      const k = e.key?.toLowerCase();
      const meta = e.metaKey || e.ctrlKey;
      if (meta && (k === 's' || k === 'p' || k === 'o' || k === 'u')) {
        e.preventDefault();
        e.stopPropagation();
      }
    };
    const preventContext = (e) => e.preventDefault();
    document.addEventListener('keydown', preventKeys, true);
    document.addEventListener('contextmenu', preventContext, true);
    return () => {
      document.removeEventListener('keydown', preventKeys, true);
      document.removeEventListener('contextmenu', preventContext, true);
    };
  }, []);

  // Track scroll container width for responsive page sizing
  useEffect(() => {
    const el = scrollRef.current;
    if (!el) return;
    const ro = new ResizeObserver((entries) => {
      for (const entry of entries) {
        const w = entry.contentRect?.width || entry.target.clientWidth || 0;
        setContainerWidth(w);
      }
    });
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  // Fullscreen API
  const requestFs = () => {
    const el = shellRef.current;
    if (!el) return;
    if (el.requestFullscreen) el.requestFullscreen();
    else if (el.webkitRequestFullscreen) el.webkitRequestFullscreen();
    else if (el.msRequestFullscreen) el.msRequestFullscreen();
  };
  const exitFs = () => {
    if (document.exitFullscreen) document.exitFullscreen();
    else if (document.webkitExitFullscreen) document.webkitExitFullscreen();
    else if (document.msExitFullscreen) document.msExitFullscreen();
  };
  useEffect(() => {
    const onFsChange = () => {
      const fs =
        document.fullscreenElement ||
        document.webkitFullscreenElement ||
        document.msFullscreenElement;
      setIsFullscreen(!!fs);
    };
    document.addEventListener('fullscreenchange', onFsChange);
    document.addEventListener('webkitfullscreenchange', onFsChange);
    document.addEventListener('msfullscreenchange', onFsChange);
    return () => {
      document.removeEventListener('fullscreenchange', onFsChange);
      document.removeEventListener('webkitfullscreenchange', onFsChange);
      document.removeEventListener('msfullscreenchange', onFsChange);
    };
  }, []);

  // Controls
  const zoomIn = () => setScale((s) => Math.min(3, parseFloat((s + 0.1).toFixed(2))));
  const zoomOut = () => setScale((s) => Math.max(0.5, parseFloat((s - 0.1).toFixed(2))));
  const fitToWidth = () => setFitMode('width');
  const fitToPage = () => setFitMode('page');

  // Compute page width so no content is cut off
  const viewportH = typeof window !== 'undefined' ? window.innerHeight : 800;
  const toolbarH = 56; // approx toolbar height
  const usableH = Math.max(200, viewportH - (isFullscreen ? toolbarH : 140)); // room for header/margins
  const pageAspect = 1 / Math.sqrt(2); // ~0.707 (A-series portrait)
  const widthForPageFit = usableH * pageAspect;

  const baseWidth =
    fitMode === 'page'
      ? Math.min((containerWidth || 0) - 24, widthForPageFit)
      : (containerWidth || 0) - 24;

  const pageRenderWidth = Math.max(220, Math.floor(baseWidth * scale));

  return (
    <div ref={shellRef} className="w-full rounded-xl bg-white border border-gray-200">
      {/* Toolbar */}
      <div className="flex items-center justify-between px-3 py-2 border-b bg-gray-50">
        <div className="flex items-center gap-2 text-sm text-gray-700">
          <button
            onClick={fitToWidth}
            className={`px-2 py-1 rounded border ${
              fitMode === 'width' ? 'bg-white border-primary-300 text-primary-700' : 'border-gray-300 hover:bg-white'
            }`}
            title="Fit to width"
          >
            <Columns className="inline w-4 h-4 mr-1" />
            Fit width
          </button>
          <button
            onClick={fitToPage}
            className={`px-2 py-1 rounded border ${
              fitMode === 'page' ? 'bg-white border-primary-300 text-primary-700' : 'border-gray-300 hover:bg-white'
            }`}
            title="Fit to page"
          >
            <Maximize className="inline w-4 h-4 mr-1" />
            Fit page
          </button>
        </div>
        <div className="flex items-center gap-2">
          <button onClick={zoomOut} className="px-2 py-1 rounded border border-gray-300 hover:bg-white" title="Zoom out">
            <ZoomOut className="inline w-4 h-4" />
          </button>
          <span className="text-gray-600 text-sm w-16 text-center select-none">{Math.round(scale * 100)}%</span>
          <button onClick={zoomIn} className="px-2 py-1 rounded border border-gray-300 hover:bg-white" title="Zoom in">
            <ZoomIn className="inline w-4 h-4" />
          </button>
          {!isFullscreen ? (
            <button
              onClick={requestFs}
              className="px-2 py-1 rounded border border-gray-300 hover:bg-white text-sm"
              title="Enter full screen"
            >
              <Maximize2 className="inline w-4 h-4 mr-1" />
              Fullscreen
            </button>
          ) : (
            <button
              onClick={exitFs}
              className="px-2 py-1 rounded border border-gray-300 hover:bg-white text-sm"
              title="Exit full screen"
            >
              <Minimize2 className="inline w-4 h-4 mr-1" />
              Exit
            </button>
          )}
        </div>
      </div>

      {/* Scrollable viewer area */}
      <div
        ref={scrollRef}
        className={`w-full ${isFullscreen ? 'h-[calc(100vh-56px)]' : 'h-[78vh]'} overflow-auto bg-gray-100 p-3 select-none`}
      >
        <Document
          file={src}
          onLoadSuccess={({ numPages }) => {
            setNumPages(numPages);
            setError('');
          }}
          onLoadError={(err) => setError(err?.message || 'Failed to load PDF')}
          loading={<div className="text-gray-600 p-4">Loading PDF</div>}
          options={{
            cMapUrl: `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/cmaps/`,
            cMapPacked: true,
          }}
        >
          {error ? (
            <div className="text-red-600 text-sm p-4">{error}</div>
          ) : (
            Array.from(new Array(numPages || 0), (_, idx) => (
              <div key={idx} className="mb-4 flex justify-center">
                <Page
                  pageNumber={idx + 1}
                  width={pageRenderWidth}
                  renderTextLayer={false}
                  renderAnnotationLayer={false}
                />
              </div>
            ))
          )}
        </Document>
      </div>
    </div>
  );
};

const QuestionBank = () => {
  const [selected, setSelected] = useState(BANKS[0]);
  const banks = useMemo(() => BANKS, []);

  // Optional: restore via ?doc=health
  useEffect(() => {
    const sp = new URLSearchParams(window.location.search);
    const doc = sp.get('doc');
    if (doc) {
      const found = banks.find((b) => b.slug === doc);
      if (found) setSelected(found);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="bg-gray-50">
      {/* Hero */}
      <section className="bg-gradient-to-r from-primary-600 to-primary-700 text-white py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <BookOpen className="w-14 h-14 mx-auto mb-4" />
            <h1 className="text-3xl md:text-4xl font-bold">Question Bank</h1>
            <p className="text-blue-100 mt-2">
              Browse curated question banks and read them online without download.
            </p>
          </div>
        </div>
      </section>

      {/* Content */}
      <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <SectionHeader
          title="Browse & Read"
          subtitle="Select a question bank on the left; it will open in the viewer."
        />

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left: List */}
          <div className="lg:col-span-1">
            <Card>
              <div className="p-5">
                <h3 className="text-lg font-semibold text-gray-800 mb-4 flex items-center gap-2">
                  <FileText className="w-5 h-5 text-primary-600" />
                  Available Question Banks
                </h3>
                <div className="space-y-3">
                  {banks.map((b) => {
                    const active = selected?.slug === b.slug;
                    return (
                      <button
                        key={b.slug}
                        onClick={() => setSelected(b)}
                        className={`w-full text-left p-4 rounded-lg border transition-all ${
                          active
                            ? 'border-primary-200 bg-primary-50'
                            : 'border-gray-200 hover:bg-gray-50'
                        }`}
                      >
                        <div className="font-medium text-gray-800">{b.title}</div>
                        <div className="text-xs text-gray-500 mt-1">{b.description}</div>
                      </button>
                    );
                  })}
                </div>

                <div className="mt-6 p-3 rounded-lg bg-yellow-50 text-yellow-800 text-xs">
                  Tip: Download is disabled on this page by design. For updates, contact the admin.
                </div>
              </div>
            </Card>
          </div>

          {/* Right: Viewer */}
          <div className="lg:col-span-2">
            <Card>
              <div className="p-5">
                <div className="flex items-center justify-between mb-3">
                  <h3 className="text-lg font-semibold text-gray-800 flex items-center gap-2">
                    <Eye className="w-5 h-5 text-primary-600" />
                    {selected?.title || 'Open a question bank'}
                  </h3>
                  <div className="text-xs text-gray-500">
                    Rightclick disabled  Print/Save shortcuts blocked
                  </div>
                </div>

                {selected?.file ? (
                  <PdfJsViewer src={selected.file} />
                ) : (
                  <div className="h-[78vh] rounded-xl bg-gray-100 flex items-center justify-center text-gray-500">
                    Select a question bank to preview
                  </div>
                )}
              </div>
            </Card>
          </div>
        </div>
      </section>
    </div>
  );
};

export default QuestionBank;
